:root {
    --azul: #191CA9;
    --black-gray: rgb(227, 233, 237);
    --blue: #4e73df;
    --indigo: #6610f2;
    --purple: #6f42c1;
    --pink: #e83e8c;
    --red: #e74a3b;
    --orange: #fd7e14;
    --yellow: #f6c23e;
    --green: #1cc88a;
    --teal: #20c9a6;
    --cyan: #36b9cc;
    --white: #fff;
    --gray: #858796;
    --gray-dark: #5a5c69;
    --primary: #4e73df;
    --secondary: #858796;
    --success: #1cc88a;
    --info: #36b9cc;
    --warning: #f6c23e;
    --danger: #e74a3b;
    --light: #f8f9fc;
    --dark: #5a5c69;
    --breakpoint-xs: 0;
    --breakpoint-sm: 576px;
    --breakpoint-md: 768px;
    --breakpoint-lg: 992px;
    --breakpoint-xl: 1200px;
    --font-family-sans-serif: "Nunito", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
    --font-family-monospace: SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
    --github: #171515;
}
.badge-primary {
    background: var(--azul) !important;
}

.bg-gradient-primary {
    background: var(--github) !important;
}

.fetchResults {
    position: absolute;
    top: 100%;
    width: 380px;
    z-index: 1;
    background: white !important;
    padding: 20px;
    border-radius: 3px;
    max-height: 400px;
    overflow-y: auto;
}

.fetchResults div p{
    max-width: 100% !important;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
}

#sidebarToggleTop {
    display: none !important;
}

@media screen and (max-width: 575px) {
    .navbar-search {
        display: block !important;
    }
    .fetchResults {
        width: 300px !important;
    }
}

@media screen and (max-width: 439px) {
    .fetchResults {
        width: 200px !important;
    }
}